import React from "react";
import {BrowserRouter, Route, Switch, Link, NavLink} from "react-router-dom";
import AddCreatePage from "../components/AddCreatePage";
import EditPage from "../components/EditPage";
import ExpensifyDashboardPage from "../components/ExpensifyDashboardPage";
import Header from "../components/Header";
import HelpPage from "../components/HelpPage";
import NotFound from "../components/NotFound";

const AppRouter = () => (
  <BrowserRouter>
    <div>
      <Header/>
      <Switch>
        <Route path="/" component={ExpensifyDashboardPage} exact={true} />
        <Route path="/create" component={AddCreatePage} />
        <Route path="/edit/:id" component={EditPage} />
        <Route path="/help" component={HelpPage} />
        <Route component={NotFound} />
      </Switch>
    </div>
  </BrowserRouter>
);

export default AppRouter;



